<template>
    <v-container grid-list-xl>
        <v-layout wrap justify-start row>
                        
           <v-flex xs12 md6 lg4 xl3 style="opacity: 0" class="animate-fade-in">
                <v-card width="90%" height="350" class="pt-4 mx-auto" :color="selectedColor">
                    <v-form
                        style="position: relative; z-index: 500 !important;"
                        @submit.prevent="addNote()"
                        class="px-4 pt-2"
                        ref="addNote"
                        v-model="valid"
                        lazy-validation
                    >
                        <ckeditor style="background-color:#f2f2f2;" :editor="editor" @ready="onReady" @input="checkFields" v-model="noteContent" :config="editorConfig"></ckeditor>
                        <div class="error--text mt-1 animate-fade-in" style="font-size: 12px;" v-if="noteContentError">{{ noteContentError }}</div>

                        <v-select
                        :items="['red', 'blue', 'green', 'yellow', 'white', 'grey']"
                        label="Choose card color"
                        v-model="selectedColor"
                        @input="changeColor()"
                        class="mt-3"
                        ></v-select>

                        <v-btn
                        :disabled="!valid"
                        class="white--text text-capitalize mt-0"
                        type="submit"
                        color="error"
                        >
                        Add
                        </v-btn>

                    </v-form>
                </v-card>
            </v-flex>

            <v-flex v-for="(note, index) in notes" :key="note.id" :id="note.id" style="opacity: 0" xs12 sm6 md6 lg4 xl3>
                <v-card width="90%" height="350" class="mb-4 pa-3 mx-auto" :color="note.backgroundColor">
                    <v-card-text v-html="note.content" style="word-wrap: break-word !important; overflow: hidden !important; max-height: 300px" class="pb-0"></v-card-text>
                    <small class="ml-3">{{ note.date }}</small>
                    <v-card-actions class="positionCardButtons">
                        <v-btn :disabled="!validDeleteBtn" @click="deleteNote(note.id, index)" class="deleteBtn" color="error" fab medium>
                            <svg xmlns="http://www.w3.org/2000/svg" width="30" height="30" viewBox="0 0 24 24"><path d="M6 19c0 1.1.9 2 2 2h8c1.1 0 2-.9 2-2V7H6v12zM19 4h-3.5l-1-1h-5l-1 1H5v2h14V4z"/><path d="M0 0h24v24H0z"/></svg>
                        </v-btn>
                    </v-card-actions> 
                </v-card>
            </v-flex>

        </v-layout>
    </v-container>
</template>

<style lang="sass">

.deleteBtn
    color: #fff !important
    svg
        fill: currentColor

.v-input__append-inner
    display: none !important

.v-select__selections
    border: 1px solid #757575
    border-radius: 3px
    padding: 5px
    margin-top: 5px
    background-color: #f2f2f2 !important
    color: grey !important

.v-select__selections::after
    content: '▼'
    position: relative
    right: 5px

.positionCardButtons
    position: absolute
    bottom: -10%
    left: 70%

.ck-blurred
    border: 1px solid grey !important

.animate-fade-in
    animation: anim-fade-in .5s ease-in-out forwards

.animate-fade-out
    animation: anim-fade-out .5s ease-in-out forwards

@keyframes anim-fade-in
    from
        opacity: 0
        transform: scale(0)

    to
        opacity: 1
        transform: scale(1)

@keyframes anim-fade-out
    from
        opacity: 1
        transform: scale(1)

    to
        opacity: 0
        transform: scale(0)

</style>

<script>
import firebase from 'firebase'
import db from './../firebase/init'
import CKEditor from '@ckeditor/ckeditor5-vue'
import DecoupledEditor from '@ckeditor/ckeditor5-build-decoupled-document'
import moment from 'moment'
import './../../node_modules/nprogress/nprogress.css'
import NProgress from 'nprogress'


export default {
            components: {
            ckeditor: CKEditor.component
        },
    data() {
        return {
            selectedColor: 'yellow',
            editor: DecoupledEditor,
            notes: [],
            valid: true,
            noteContent: '',
            noteContentError: null,
            editorConfig: {

                highlight: {
                    options: [
                        {
                            model: 'greenPen',
                            class: 'pen-green',
                            title: 'White pen',
                            color: 'var(--ck-highlight-pen-green)',
                            type: 'pen'
                        },
                        {
                            model: 'redPen',
                            class: 'pen-red',
                            title: 'Red pen',
                            color: 'var(--ck-highlight-pen-red)',
                            type: 'pen'
                        },
                        {
                            model: 'redMarker',
                            class: 'marker-red',
                            title: 'Red marker',
                            color: 'var(--ck-highlight-pen-red)',
                            type: 'marker'
                        },
                        {
                            model: 'greenMarker',
                            class: 'marker-green',
                            title: 'Green marker',
                            color: 'var(--ck-highlight-marker-green)',
                            type: 'marker'
                        },

                        {
                            model: 'yellowMarker',
                            class: 'marker-yellow',
                            title: 'Yellow marker',
                            color: 'var(--ck-highlight-marker-yellow)',
                            type: 'marker'
                        },
                        {
                            model: 'blueMarker',
                            class: 'marker-blue',
                            title: 'Blue marker',
                            color: 'var(--ck-highlight-marker-blue)',
                            type: 'marker'
                        },
                        {
                            model: 'pinkMarker',
                            class: 'marker-pink',
                            title: 'Pink marker',
                            color: 'var(--ck-highlight-marker-pink)',
                            type: 'marker'
                        },
                    ]
                },
                placeholder: 'Type Here',
                removePlugins: [ 'Easy Image', 'Image', 'ImageCaption', 'ImageStyle', 'ImageToolbar', 'ImageUpload', 'Table', 'TableToolbar', 'BlockQuote', 'MediaEmbed']
            },
            submitting: null,
            validDeleteBtn: true
        }
    },

    methods: {
        onReady( editor )  {
            // Insert the toolbar before the editable area.
            editor.ui.getEditableElement().parentElement.insertBefore(
                editor.ui.view.toolbar.element,
                editor.ui.getEditableElement()
            );
        },
        checkFields() {
            if(!this.submitting) {
                if(this.noteContent.length < 1) {
                    this.noteContentError = "Content cannot be empty"
                    this.valid = false
                } else if(this.noteContent.length > 400) {
                    this.noteContentError = "Content can't be longer than 400 characters"
                    this.valid = false
                } else if(this.noteContent.length < 400 && this.noteContent.length > 0) {
                    this.valid = true
                    this.noteContentError = null
                }
            }
        },


        addNote() {
            if(this.noteContent.length < 400 && this.noteContent.length > 0) {
                    NProgress.start()
                    this.valid = false
                    this.validDeleteBtn = false
                    this.submitting = true
                    this.noteContentError = null
                    let currDate = moment(String(Date())).format('MM/DD/YYYY hh:mm')
                    db.collection('users').doc(firebase.auth().currentUser.uid).collection('notes').add({
                    content: this.noteContent,
                    backgroundColor: this.selectedColor,
                    date: currDate
                    }).then(() => {
                        this.noteContent = ''
                        this.selectedColor = 'yellow'
                    }).then(() => {
                        this.valid = true
                        this.validDeleteBtn = true
                        this.submitting = null
                        NProgress.done()
                    })
                } else if (this.noteContent.length > 400) {
                    this.noteContentError = "Content can't be longer than 400 characters"
                    this.valid = false
                } else if (this.noteContent.length < 1) {
                    this.noteContentError = "Content cannot be empty"
                    this.valid = false
                }
        },

        deleteNote(noteID) {
            NProgress.start()
            let targetElement = document.getElementById(noteID)
            targetElement.classList.remove('animate-fade-in')
            targetElement.classList.add('animate-fade-out')
            this.valid = false
            this.validDeleteBtn = false
            setTimeout(() => {
                targetElement.style.display = 'none'
                db.collection('users').doc(firebase.auth().currentUser.uid).collection('notes').doc(noteID).delete().then(() => {
                    this.valid = true
                    this.validDeleteBtn = true
                    NProgress.done()
                })
            }, 500)

        }
    },
    created() {
            let user_id = firebase.auth().currentUser.uid
            db.collection('users').doc(user_id).get().then(doc => {
                this.$store.state.currentTheme = doc.data().currentTheme
            })
            db.collection('users').doc(firebase.auth().currentUser.uid).collection('notes').orderBy('date').onSnapshot(snapshot => {
            snapshot.docChanges().forEach(change => {
            if(change.type == 'added') {
                let note = change.doc.data()
                note.id = change.doc.id
                this.notes.unshift(note)
                setTimeout(() => {
                    document.getElementById(note.id).classList.add('animate-fade-in')
                }, 1)
            }
            }) 
        })
        }
    }
</script>
